import React from "react";
import { SearchBarScreen } from "../Search/SearchBarScreen";
import "./index.scss";
import { useFetchDetailItem } from "../../hooks/useFetchDetailItem";
import { WEBSERVICE } from "../../config/webservices";
import { Loading } from "../utils_components/loading/Loading";
import { Breadcrums } from "../utils_components/breadcrumb/Breadcrums";

export const ItemDetailScreen = (props) => {
  const { loading, data } = useFetchDetailItem([
    /*a nivel de backend se consumen 3 servicios que permiten obtener:
    - Item
    - Descripción
    - Categorías
  */
    `${WEBSERVICE.GET_ITEM_ID}${props.match.params.id}`,
    `${WEBSERVICE.GET_ITEM_ID}${props.match.params.id}/description`,
    `${WEBSERVICE.GET_CATEGORY}`,
  ]);

  return (
    <>
      <SearchBarScreen props={props} />
      {loading ? <Loading /> : ""}
      {data && data.item_breadcrumb.categories ? (
        <>
          <Breadcrums categories={data.item_breadcrumb.categories} />
          <div className="container_fluid">
            <div className="container_detail">
              <div className="container_picture">
                <img src={data.item_detail.data.picture} alt={data.item_detail.data.item.title} className="picture_detail" />
              </div>
              <div className="details_item">
                <p className="item_detail_condition">
                  {data.item_detail.data.condition} - {data.item_detail.data.sold_quantity} vendidos{" "}
                </p>
                <p className="title_detail">{data.item_detail.data.item.title}</p>
                <p className="price_detail">{data.item_detail.data.price.amount}</p>
                <button className="button_primary btn_buy">Comprar</button>
              </div>
            </div>
            <div className="container_description">
              <h4 className="title_description">Descripción del Producto</h4>
              <p className="detail_description">{data.item_description.data.description}</p>
            </div>
          </div>
        </>
      ) : (
        <p className="no_results">No hay resultados para mostrar</p>
      )}
    </>
  );
};
/*: { item_detail = false, item_description = false, item_breadcrumb = false */
