import { useState, useEffect, useRef } from "react";
export const useFetchDetailItem = (url_webservices) => {
  const isMounted = useRef(true);
  const [state, setState] = useState({ data: null, loading: true, error: null });

  useEffect(() => {
    return () => {
      isMounted.current = false;
    };
  }, []);

  useEffect(() => {
    setState({ data: null, loading: true, error: null });

    new Promise((resolve_item_detail, reject_item_detail) => {
      fetch(url_webservices[0])
        .then((resp) => resp.json())
        .then((item_detail) => {
          resolve_item_detail(true);
          new Promise((resolve_item_description, reject_item_description) => {
            fetch(url_webservices[1])
              .then((resp) => resp.json())
              .then((item_description) => {
                resolve_item_description(true);
                if (item_description.ok) {
                  new Promise((resolve, reject) => {
                    fetch(url_webservices[2] + item_detail.data.category_id)
                      .then((resp) => resp.json())
                      .then((item_breadcrumb) => {
                        if (isMounted.current) {
                          setState({
                            loading: false,
                            error: null,
                            data: {
                              item_detail,
                              item_description,
                              item_breadcrumb,
                            },
                          });
                        }
                        resolve(true);
                      });
                  });
                } else {
                  setState({
                    loading: false,
                    error: null,
                    data: null,
                  });
                }
              });
          });
        });
    });
  }, []);

  return state;
};
